<template>
  <div>
    <div class="shopcart">
      <div class="content">
        <div class="content-left">
          <div class="logo-wrapper" @click="toggleList">
            <div class="logo" :class="{hightlight: totalCount > 0}">
              <span class="icon-shopping_cart" :class="{hightlight: totalCount > 0}"></span>
            </div>
            <div class="num" v-show="totalCount > 0">{{totalCount}}</div>
          </div>
          <div class="price" :class="{hightlight: totalPrice > 0}">￥{{totalPrice}}</div>
          <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
        </div>
        <div class="content-right" @click="pay">
          <div class="pay" :class="payClass">{{payDesc}}</div>
        </div>
        <div class="ball-container">
          <div v-for="ball in balls" :key="ball.index">
            <transition name="drop" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
              <div v-show="ball.show" class="ball">
                <div class="inner inner-hook"></div>
              </div>
            </transition>
          </div>
        </div>
        <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="empty">清空</span>
          </div>
          <div class="list-content" ref="listConent">
            <ul class="left">
              <li class="food" v-for="food  in selectFoods" :key="food.index">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>￥{{food.price * food.count}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
        </transition>
      </div>
    </div>
    <!-- <transition name="fade"> -->
    <div class="list-mask" @click="hiddenList" v-show="listShow"></div>
    <!-- </transition> -->
  </div>
</template>

<script>
import BScroll from 'better-scroll';
import cartcontrol from '../cartcontrol/cartcontrol';

export default {
  props: {
    // 从goods组件获得选择的food，传给子组件shopcart ，selectFoods获得food的价格和数量
    //  selectFoods 很关键，所有的状态都是这个而来，，数据驱动状态，状态驱动视图
    selectFoods: {
      type: Array,
      default() {
        return [
          {
            price: 10,
            count: 1
          }
        ];
      }
    },
    // 配送费
    deliveryPrice: {
      type: Number,
      default: 0
    },
    // 起送费
    minPrice: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      balls: [
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        },
        {
          show: false
        }
      ],
      dropBalls: [],
      // 默认不显示，折叠
      fold: true
    };
  },
  components: {
    cartcontrol
  },
  // 通过计算，动态获取价格 ，数量 ， 价格= prcie* count
  computed: {
    listShow() {
      if (!this.totalCount) {
        /* eslint-disable-next-line */
        this.fold = true;
        return false;
      }
      let show = !this.fold;
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            /* eslint-disable-next-line */
            this.scroll = new BScroll(this.$refs.listConent, {
              click: true
          });
          } else {
            this.scroll.refresh();
          }
        });
      }
      return show;
    },
    // 总价
    totalPrice() {
      let total = 0;
      // 遍历selecFoods
      this.selectFoods.forEach((food) => {
        total += food.price * food.count;
      });
      return total;
    },
    // 选择的总数
    totalCount() {
      let count = 0;
      // 遍历selectFoods
      this.selectFoods.forEach((food) => {
        count += food.count;
      });
      return count;
    },
    // 右侧两种状态
    payDesc() {
      if (this.totalPrice === 0) {
        return `￥${this.minPrice}元起送`;
      } else if (this.totalPrice < this.minPrice) {
        let diff = this.minPrice - this.totalPrice;
        return `还差￥${diff}元起送`;
      } else {
        return '去结算';
      }
    },
    payClass() {
      if (this.totalPrice < this.minPrice) {
        return 'not-enough';
      } else {
        return 'enough';
      }
    }
  },
  methods: {
    pay() {
      if (this.totalPrice < this.minPrice) {
        return;
      }
      window.alert(`支付${this.totalPrice}元`);
    },
    hiddenList() {
      this.fold = true;
    },
    empty() {
      // 遍历selectfoods
      this.selectFoods.forEach((food) => {
        // 设置每个food的值为0
        food.count = 0;
      });
    },
    toggleList() {
      if (!this.totalCount) {
        return;
      }
      this.fold = !this.fold;
    },
    drop(el) {
      for (let i = 0; i < this.balls.length; i++) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = el;
          this.dropBalls.push(ball);
          return;
        }
      }
    },
    beforeEnter(el) {
      let count = this.balls.length;
      while (count--) {
        let ball = this.balls[count];
        if (ball.show) {
          let rect = ball.el.getBoundingClientRect();
          let x = rect.left - 32;
          let y = -(window.innerHeight - rect.top - 22);
          el.style.display = '';
          el.style.webkitTransform = `translate3d(0,${y}px, 0)`;
          el.style.transform = `translate3d(0,${y}px, 0)`;
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = `translate3d(${x}px, 0, 0)`;
          inner.style.transform = `translate3d(${x}px, 0, 0)`;
        }
      }
    },
    enter(el, done) {
      /* eslint-disable no-unused-vars */
      let rf = el.offsetHeight;
      this.$nextTick(() => {
        el.style.webkitTransform = 'translate3d(0, 0, 0)';
        el.style.transform = 'translate3d(0, 0, 0)';
        let inner = el.getElementsByClassName('inner-hook')[0];
        inner.style.webkitTransform = 'translate3d(0, 0, 0)';
        inner.style.transform = 'translate3d(0, 0, 0)';
        el.addEventListener('transitionend', done);
      });
    },
    afterEnter(el) {
      let ball = this.dropBalls.shift();
      if (ball) {
        ball.show = false;
        el.style.display = 'none';
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
  @import "../../common/stylus/mixin.styl"
  .shopcart
    position: fixed
    left: 0
    bottom: 0
    z-index: 50
    width: 100%
    height: 48px;
    .content
      display: flex
      background: #141d27
      font-size: 0
      .content-left
        flex: 1
        .logo-wrapper
          display: inline-block
          position: relative
          top: -10px
          margin: 0 12px
          padding: 6px
          width : 56px
          height: 56px
          box-sizing: border-box
          vertical-align: top
          border-radius: 50%
          background: #141d27
          .logo
            width: 100%
            height: 100%
            border-radius: 50%
            background: #2b343c
            text-align: center
            &.hightlight
              background: rgb(0, 160, 220)
            .icon-shopping_cart
              font-size: 24px
              color: #80858a
              line-height: 44px
              &.hightlight
                color: #fff
        .num
          position: absolute
          top: 0
          right: 0
          width: 24px
          height: 16px
          line-height: 16px
          text-align: center
          border-radius: 16px;
          font-size: 9px
          font-weight: 700
          color: #fff
          background: rgb(240, 20, 20)
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4)
        .price
          display: inline-block
          vertical-align: top
          line-height: 24px
          margin-top: 12px
          padding-right: 12px
          box-sizing: border-box
          border-right: 1px solid rgba(255, 255, 255, 0.1)
          font-size: 16px
          font-weight: 700
          color: rgba(255, 255, 255, 0.4)
          &.hightlight
            color: #fff
        .desc
          display: inline-block
          vertical-align: top
          line-height: 24px
          margin: 12px 0 0 12px
          line-height: 24px
          color: rgba(255, 255, 255, 0.4)
          font-size: 10px
      .content-right
        flex: 0 0 105px
        width: 105px
        .pay
          height: 48px
          line-height: 48px
          text-align: center
          font-size: 12px
          font-weight: 700
          color: rgba(255, 255, 255, 0.4)
          background: #2b333b
          &.not-enough
            background: #2b333b
          &.enough
            background: #00b43c
            color: #fff
    .ball-container
      &.drop-transition
        transition: all 0.4s
        .inner
          width: 16px
          height: 16px
          border-radius: 50%
          background: rgb(0, 160, 220)
          transition: all 0.5s
      .ball
        position: fixed
        left: 32px
        bottom: 22px
        z-index: 200
    // &.fold-enter, &.fold-leave-to
    //   // transition: all 0.5s
    //   transform: translate3d(0, -100%, 0)
    //   opacity: 0
    // &.fold-leave-active
    //   transition: all .8s
    //   transform: translate3d(0, 0, 0)
      // position: absolute
    .shopcart-list
      position: absolute
      left: 0
      // top: -90px
      bottom: 50px
      z-index: -1
      width: 100%
      .list-header
        height: 45px
        line-height: 40px
        padding: 0 18px
        background: #f3f5f7
        border-bottom: 1px solid rgba(7, 17, 27, 0.1)
        .title
          float: left
          font-size: 14px
          line-height: 25px
          color: rgb(7, 17, 27)
        .empty
          float: right
          font-size: 12px
          color: rgb(0, 160, 220)
      .list-content
        padding:0px
        width: 100%
        max-height: 217px
        background: #fff
        overflow: hidden
        .left
          padding: 0
          margin: 0
          .food
            position: relative
            padding: 12px 12px
            box-sizing: border-box
            border-1px(rgba(7, 17, 27, 0.1))
            .name
              line-height: 24px
              font-size: 14px
              color: rgb(7, 17, 27)
            .price
              position: absolute
              right: 90px
              bottom: 12px
              padding-right: 12px
              line-height: 24px
              font-size: 14px
              font-weight: 700
              color: #f01414
            .cartcontrol-wrapper
              position: absolute
              right: 0
              bottom: 6px
              padding-right: 12px
  .list-mask
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 100%
    z-index: 40
    // opacity: 0.3
    background: rgba(7, 17, 27, 0.7)
    // transition: all 0.5s ease
    backdrop-filter: blur(10px)
  // &.fade-enter-active
  //   // transition: all .5s
  //   transition: opacity .5s
  //   background: rgba(7, 17, 27, 0.6)
  // &.fade-enter, &.fade-leave
  //   opacity: 0
  //   background: rgba(7, 17, 27, 0)
</style>



